/* ForgotPassword.css */

.reset-password-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;

}

.reset-password-container h2 {
  text-align: center;
  margin-bottom: 20px;
  
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="email"],
input[type="text"],
input[type="password"] {
  width: 92%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 20px;
}

button {
  width: 100%;
  height: auto;
  padding: 10px;
  font-size: 16px;
  color: #fff;
  background-color: #ff3700;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #b32d00;
}

.error-message {
  color: #dc3545;
  margin-top: 10px;
}

.success-message {
  color: #28a745;
  margin-top: 10px;
}
